<template>
    <view
        class="page"
    >
        <m-header
            left-color="#fff"
            :sty="hStyle"
            title="测试标题测试标题测试标题测试标题测试标题"
        />
        <m-douyin
            :options="options"
            @change="change"
        >
            <view
                v-for="item in list"
                :key="item"
                :style="{...itemStyle, background: item}"
                class="item"
            >
                {{ item }} {{ index }}
            </view>
        </m-douyin>
    </view>
</template>

<script lang="ts" setup>
import { onMounted, ref, nextTick, getCurrentInstance, computed, watch } from 'vue'

// 盒子宽高
const height = ref(0)
const width = ref(0)

// 当前第几个
const index = ref(0)
// 数据列表
const list = ref(['red', 'blue', 'pink', 'yellow'])
// 加载数据中
const loading = ref(false)
// 透明度
const opacity = ref(1)
// top
const top = ref(0)
// 是否是下拉
const isDown = ref(false)

const itemStyle = computed(() => {
    return {
        width: `${width.value}px`,
        height: `${height.value}px`,
    }
})

const hStyle = computed(() => {
    if (!isDown.value) return {
        opacity: 1,
        transition: 'all 0.1s',
        top: 0
    }

    return {
        opacity: opacity.value,
        transition: 'all 0.5s',
        top: `${top.value / 2}px`
    }
})

const options = computed({
    get() {
        return {
            len: list.value.length,
            index: index.value,
            height: height.value,
            width: width.value,
            loading: loading.value, // 数据加载中
            maxH: uni.upx2px(200) * -1, // 上拉最大距离
            scrollH: uni.upx2px(200), // 上拉最大距离
            dH: uni.$m.sys.customBar
        }
    },
    set(opt) {
        const { type, value } = opt
        switch (type){
        case 'isUp':
            loading.value = value
            setTimeout(() => {
                loading.value = false
            }, 3000)
            break
        case 'setIndex':
            index.value = value
            break
        case 'isDown':
            loading.value = value
            setTimeout(() => {
                loading.value = false
                isDown.value = false
            }, 3000)
            break
        case 'header':
            opacity.value = value.opacity
            isDown.value = true
            top.value = value.top
            break
        default:
            break
        }
    }
})

// 监听值改变
const change = data => (options.value = data)

onMounted(() => {
    const instance = getCurrentInstance().proxy

    nextTick(() => {
        uni.createSelectorQuery().select('.page').boundingClientRect(data => {
            const { width: w = 0, height: h = 0} = data
            height.value = h
            width.value = w
        }).exec()
    })
})
</script>

<style lang="scss" scoped>
.page {
    width: 100vw;
    height: 100vh;

    .item {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 60rpx;
    }
}
</style>
